<template>
	<div id="takeout-grantnum">
		<public-header title="积分赠送"></public-header>
		<div class="content">
			<van-cell-group>
				<van-cell>
					<div slot="title">积分赠送状态</div>
					<iswitch :value="order.grant_credit.credit1.status" condition-open="1" condition-close="0" slot="right-icon" text-open="ON" text-close="OFF" @change="jsToggleSwitch" keys="order.grant_credit.credit1.status"></iswitch>
				</van-cell>
			</van-cell-group>
			<van-radio-group v-model="order.grant_credit.credit1.grant_type" class="margin-10-t">
				<van-cell-group>
					<van-cell title="积分赠送模式">
						<div slot="right-icon" class="c-gray" @click="onTogglePopup('credit1')">
							具体设置<van-icon name="arrow"></van-icon>
						</div>
					</van-cell>
					<van-cell title="固定比例" clickable :label="order.grant_credit.credit1.grant_type == 2 ? `按比例赠送${order.grant_credit.credit1.grant_num_2}积分` : ''" @click="order.grant_credit.credit1.grant_type = 2" class="border-0px">
						<van-radio :name="2" />
					</van-cell>
					<van-cell title="固定积分" clickable :label="order.grant_credit.credit1.grant_type == 1 ? `固定赠送${order.grant_credit.credit1.grant_num_1}积分` : ''" @click="order.grant_credit.credit1.grant_type = 1" class="border-0px">
						<van-radio :name="1" />
					</van-cell>
				</van-cell-group>
			</van-radio-group>
			<div class="padding-15">
				<van-button size="normal" type="primary" :disabled="!islegal" block @click="onSubmit">保存修改</van-button>
			</div>
		</div>
		<van-popup v-model="popupStatus.credit1" position="bottom">
			<div class="popup-title">
				<span class="c-gray" @click="onTogglePopup('credit1')">取消</span>
				<span v-if="order.grant_credit.credit1.grant_type == 2">积分赠送模式(按比例)</span>
				<span v-else-if="order.grant_credit.credit1.grant_type == 1">积分赠送模式(固定)</span>
				<span class="c-primary"  @click="onTogglePopup('credit1')">确定</span>
			</div>
			<div class="popup-content">
				<template v-if="order.grant_credit.credit1.grant_type == 1">
					<van-cell-group>
						<van-cell>
							<div slot="title">固定积分</div>
						</van-cell>
						<van-cell>
							<div slot="title">每单固定</div>
							<div slot="right-icon">
								<div class="input-unit">
									<input type="text" v-model="order.grant_credit.credit1.grant_num_1"><span>积分</span>
								</div>
							</div>
						</van-cell>
					</van-cell-group>
				</template>
				<template v-if="order.grant_credit.credit1.grant_type == 2">
					<van-cell-group>
						<van-cell>
							<div slot="title">固定比例</div>
						</van-cell>
						<van-cell>
							<div slot="title">按1元赠送</div>
							<div slot="right-icon">
								<div class="input-unit">
									<input type="text" v-model="order.grant_credit.credit1.grant_num_2"><span>积分的比例赠送</span>
								</div>
							</div>
						</van-cell>
					</van-cell-group>
				</template>
			</div>
		</van-popup>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import iswitch from "@/components/switch"
	import {getData, setData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				showPreLoading: true,
				islegal: false,
				order: {
					grant_credit:{
						credit1:{
							grant_num: '0',
							grant_type: '1',
							status: '1',
							grant_num_1: '0',
							grant_num_2: '0',
						}
					},
				},
				popupStatus: {
					credit1: false
				}
			}
		},
		components: {
			publicHeader,
			iswitch
		},
		methods: {
			onLoad(){
				var that = this;
				getData({
					vue: this,
					autoAssign: true,
					variable: 'order',
					url: 'plateform/config/takeout/order',
					success: (res) => {
						that.islegal = true;
						if (res.order.grant_credit.credit1.grant_type == '1'){
							this.order.grant_credit.credit1.grant_num_1 = res.order.grant_credit.credit1.grant_num;
							this.order.grant_credit.credit1.grant_num_2 = '0';
						}
						if(res.order.grant_credit.credit1.grant_type == '2'){
							this.order.grant_credit.credit1.grant_num_2 = res.order.grant_credit.credit1.grant_num;
							this.order.grant_credit.credit1.grant_num_1 = '0';
						}
					}
				});
			},

			jsToggleSwitch(res){
				this.util.jsToggleSwitch({
					vue: this,
					key: res.key || res.keys,
					value: res.value
				});
			},

			onSubmit(){
				var that = this;
				if(!that.islegal) {
					return false;
				}
				that.islegal = false;
				setData({
					vue: this,
					url: 'plateform/config/takeout/order',
					data: that.order,
					redirect: that.util.getUrl({path: '/pages/config/index'}),
					fail: function(res) {
						that.util.$toast(res);
						that.islegal = true;
						return;
					}
				});
			},

			onTogglePopup(type) {
				this.popupStatus[type] = !this.popupStatus[type];
			},
		},
		mounted(){
			this.onLoad();
		}
	}
</script>

<style>
	
</style>


